
"use server";

import { revalidatePath } from "next/cache";
import { firestore } from "@/lib/firebase";// Este es el admin-sdk, no? a ver... si.
import { collection, addDoc, doc, setDoc } from "firebase/firestore";

/**
 * Registra una nueva mascota para un usuario específico.
 * @param {string} userId - El UID del dueño de la mascota.
 * @param {object} mascotaData - Los datos de la mascota del formulario.
 * @returns {Promise<{success: boolean, error?: string}>}
 */
export async function registrarMascota(userId, mascotaData) {
  if (!userId) {
    return { success: false, error: "Usuario no autenticado." };
  }

  const { nombre, especie, raza, fechaNacimiento, sexo } = mascotaData;

  if (!nombre || !especie || !fechaNacimiento || !sexo) {
    return { success: false, error: "Faltan campos obligatorios." };
  }

  try {
    const mascotasCollectionRef = collection(firestore, `users/${userId}/mascotas`);
    
    // addDoc genera un ID automático para la nueva mascota
    await addDoc(mascotasCollectionRef, {
        nombre,
        especie,
        raza: raza || '', // Campo opcional
        fechaNacimiento,
        sexo,
        creadoEn: new Date().toISOString(),
    });

    // Invalida el caché de la página de mascotas para que la lista se actualice.
    revalidatePath("/mascotas");

    return { success: true };

  } catch (error) {
    console.error("Error al registrar la mascota:", error);
    return { success: false, error: "No se pudo registrar la mascota en la base de datos." };
  }
}
